.service-card {
  background-color: $c-white;
  border-radius: 14px;
  padding: 32px;
  transition: all 0.3s;

  &__num {
    width: 46px;
    height: 24px;
    border-radius: 23px;
    background-color: rgba(32, 96, 246, 0.1);
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-size: 13px;
    line-height: 24px;
    text-align: center;
    text-transform: uppercase;
    color: $c-blue;
  }

  &__text {
    font-family: "Open Sans", sans-serif;
    font-weight: 600;
    font-size: 13px;
    line-height: 150%;
    color: $c-light-grey;
    padding: 24px 0 32px;
  }

  &__link {
    cursor: pointer;
  }

  &:hover {
    transform: scale(1.03);
    box-shadow: 0 0 20px $c-light-grey;
  }
}
